        <p className="padding5_top">A great and simple visual hierarchy will help your email convert more. With regards to typography, make it large : we recommend to use 16px font size for text (20 for desktop) and 22 for headings. Try to keep enough white space to make the color of your Call To Action more vivid. CTA buttons should be 44px according to Apple but Google claims that 48px would be better. Play around with heading and text contrasts. Make sure your branding is consistent in your email. Oh and now that we are talking CTA, did you check if your landing page is responsive too ? Be cautious when using navbar. If you do so, make sure that it is a key element in your action hierarchy. If it dilutes the visual weight of your main Call To Action, remove the navbar. PS : make also sure your branding is consistent in your email. </p>
