
import React from "react";

export default class MyFormHero extends React.Component {
  constructor(props) {
    super(props);
    this.submitForm = this.submitForm.bind(this);
    this.state = {
      status: ""
    };
  }

  render() {
    const { status } = this.state;
    return (
        <div className="card-section">
            <form
            onSubmit={this.submitForm}
            action="https://formspree.io/mnbbobvm"
            method="POST"
            >
                <label htmlFor="TestMySite_Name">Website or App url</label>
                <input type="text" className="login-box-input" name="TestMySite_Domain" id="userName" required data-error="NEW ERROR MESSAGE" placeholder="domain.com"/>
                <label htmlFor="userEmail">Email</label>
                <input type="email" className="login-box-input" name="TestMySite_Email" id="userEmail" required data-error="NEW ERROR MESSAGE" placeholder="name@email.com"/>
                <div className="padding5_top">
                    {status === "SUCCESS" ? <input className="button" type="submit" id="send_message" name="submit" value="Form Successfully Submitted. Thank you!" /> : <input className="button" type="submit" id="send_message" name="submit" value="Test my site" />}
                    {status === "ERROR" && <input className="button" type="submit" id="send_message" name="submit" value="Ooops! There was an error." />}
                 
                </div>
            </form>
        </div>
    );
  }

  submitForm(ev) {
    ev.preventDefault();
    const form = ev.target;
    const data = new FormData(form);
    const xhr = new XMLHttpRequest();
    xhr.open(form.method, form.action);
    xhr.setRequestHeader("Accept", "application/json");
    xhr.onreadystatechange = () => {
      if (xhr.readyState !== XMLHttpRequest.DONE) return;
      if (xhr.status === 200) {
        form.reset();
        this.setState({ status: "SUCCESS" });
      } else {
        this.setState({ status: "ERROR" });
      }
    };
    xhr.send(data);
  }
}